export async function getFormData(id: string): Promise<FormData | null> {
  return await storage.get(FORM_KEY_PREFIX + id, null)
}

type PopupStateParam = {
  pos: "fillForm" | "settings" //|"fillFormSettings";
  url: string
  fieldDatas?: {
    key: string
    options: any
  }[]
  formId: string
}

type PopupState =
  | null
  | (PopupStateParam & { createdAt: number; lastUpdatedAt: number })

export async function saveCurrentPopupState(state: PopupStateParam) {
  return storage.set("popup-state", {
    ...state,
    createdAt: Date.now(),
    lastUpdatedAt: Date.now(),
  })
}

export async function getCurrentPopupState(currentUrl: string) {
  const state: PopupState = await storage.get("popup-state", null)
  if (!state) return state
  resetCurrentPopupState()
  //logic
  const now = Date.now()
  switch (state.pos) {
    /*case "fillFormSettings":
      //above 1 minute
      if (now - state.last > 1000 * 60) {
        return null
      }*/
    case "fillForm":
      if (state.url != currentUrl) return null
      //above 1 minute
      if (now - state.lastUpdatedAt > 1000 * 60) {
        return null
      }
      return state
    case "settings":
      //above 1 minute
      if (now - state.lastUpdatedAt > 1000 * 60) {
        return null
      }
      return state
  }
}

export async function resetCurrentPopupState() {
  return saveCurrentPopupState(null)
}

export async function deleteFormData(id: string) {
  let forms: FormListItem[] = await storage.get(FORM_LIST_KEY, [])
  storage.set(
    FORM_LIST_KEY,
    forms.filter((form) => form.id != id)
  )
  storage.set(FORM_KEY_PREFIX + id, null)
}

export async function saveFormData(
  data: Partial<FormData>,
  status: "new" | "edit",
  id: string
): Promise<FormListItem> {
  let forms: FormListItem[] = await storage.get(FORM_LIST_KEY, [])
  let formInfo: FormListItem
  if (status == "new") {
    let id = makeid(4)
    while (forms.some((e) => e.id == id)) id = makeid(4)
    data.id = id
    forms.push({ name: data.name, id })
    await storage.set(FORM_LIST_KEY, forms)
    await storage.set(FORM_KEY_PREFIX + id, sanitizeFormData(data as FormData))
    formInfo = { id: id, name: data.name }
  } else {
    forms = forms.map((e) =>
      e.id != id
        ? e
        : { name: data.name, id, favorite: e.favorite === true ? true : false }
    )
    formInfo = { id: id, name: data.name }
    await storage.set(FORM_LIST_KEY, forms)
    await storage.set(FORM_KEY_PREFIX + id, sanitizeFormData(data as FormData))
  }
  print("storage size: ", JSON.stringify(data).length)
  return formInfo
}

function sanitizeFormData(data: FormData): FormData {
  console.log("colection !!!", data.collection)
  return {
    ...data,
    collection: { ...data.collection },

    name: (data.name.length && data.name) || "Untitled",
    daps: data.daps.filter((field) => {
      return field.property != null
    }),
  }
}

function copyBlockOperation(
  block: BlockType,
  parentId: string | undefined,
  blocksMap: Record<string, BlockType>,
  currentId?: string
) {
  const newBlockId = currentId || uuid()
  let op = {
    id: newBlockId,
    table: "block",
    path: [],
    command: "update",
    args: {} as any,
  }
  let ops = [op]
  if (block.role == "none") {
    return {
      id: newBlockId,
      ops: [{ ...op, args: { type: "text", parent_id: parentId } }],
    }
  }
  let blockVal = block.value
  op.args = {
    ...blockVal,
    id: newBlockId,
    parent_id: parentId,
    copied_from: blockVal.id,
  }
  //remove key from args
  ;["is_template", "created_time", "last_edited_time"].forEach((key) => {
    delete op.args[key]
  })

  if (blockVal.content) {
    const childResults = blockVal.content.map((id) =>
      copyBlockOperation(blocksMap[id], newBlockId, blocksMap)
    )
    let newContent = childResults.map((op) => op.id)
    op.args.content = newContent
    ops.push(...childResults.flatMap((res) => res.ops))
  }
  return {
    id: newBlockId,
    ops,
  }
}

export async function getTransacOperationsFromTemplate(
  blocksMap: Record<string, BlockType>,
  templateId: string,
  currentId: string
) {
  let res = copyBlockOperation(
    blocksMap[templateId],
    undefined,
    blocksMap,
    currentId
  )
  return res.ops
}

