export async function getUpToDateTemplateBlocksMap(
  client: DummyNotionClient,
  templateId: string,
  refreshTemplate: boolean
) {
  let content
  if (refreshTemplate) {
    print("refreshing the template...")
    content = await fetchFullContentPage(templateId, client.authToken)
    await storage.set(`TEMPLATE-${templateId}`, content)
  } else {
    content = await storage.get(`TEMPLATE-${templateId}`, null)
    if (content == null) {
      content = await getUpToDateTemplateBlocksMap(client, templateId, true)
    } else {
      print("got template from cache!")
    }
  }
  print(content, "\x1b[43m -- content template \x1b[0m")
  return content
}

export function throttle(fn, waitMs: number) {
  let last: Date
  let prevFn: any
  let prevTimer: any
  return (...args) => {
    const now = new Date()
    if (prevFn) {
      prevFn(undefined)
      clearTimeout(prevTimer)
      prevFn = null
    }
    return new Promise((resolve) => {
      const x = last
        ? Math.max(0, waitMs - (now.getTime() - last.getTime()))
        : 0
      prevFn = resolve
      prevTimer = setTimeout(function () {
        last = now
        prevFn = null
        resolve(fn.apply(null, args))
      }, x)
    })
  }
}

export type RefreshCollectionResult = {
  needToResetCollection: boolean
  fieldsToModify: { key: string; action: "remove" | "removeDefaultValue" }[]
  collection?: Collection
  needToRefreshTemplate?: boolean
  needToRemoveTemplate?: boolean
}
const extraProperty = ["pageCover", "pageFrontImage", "pageIcon"]
export function applyRefreshCollection(
  result: RefreshCollectionResult,
  oldDaps: Dap[]
): { collection: Collection; daps: Dap[] } {
  if (result.needToResetCollection) {
    return { daps: oldDaps, collection: undefined }
  }
  let daps: Dap[] = []
  for (let d of oldDaps) {
    let mField = result.fieldsToModify.find((f) => f.key == d.key)
    //print("old dap", d)
    let nproperty: ModifiedDapProperty
    if (d.property) {
      if (extraProperty.some((e) => e == d.property.id)) {
        print("reaplie", d.property)
        nproperty = d.property
      } else {
        nproperty = {
          ...result.collection.schema[d.property.id],
          id: d.property.id,
        }
      }
    }
    if (!mField) {
      daps.push({ ...d, property: nproperty })
      continue
    }
    switch (mField.action) {
      case "remove":
        continue
      case "removeDefaultValue":
        daps.push({ ...d, property: nproperty, options: {} })
        break
    }
  }
  return { collection: result.collection, daps }
}
export function applyRefreshCollectionOnData(
  result: RefreshCollectionResult,
  oldData: FormData
): FormData {
  const { collection, daps } = applyRefreshCollection(result, oldData.daps)
  return {
    ...oldData,
    collection,
    daps,
    template: (!result.needToRemoveTemplate && oldData.template) || undefined,
    needToRefreshTemplate: result.needToRefreshTemplate,
  }
}
async function getTemplateInfo(
  client: DummyNotionClient,
  pageId: string
): Promise<TemplateInfo> {
  const resp = await client.agent.loadPageChunk({
    pageId: pageId,
    limit: 1,
    cursor: {
      stack: [[{ id: pageId, table: "block", index: 0 }]],
    },
    chunkNumber: 0,
    verticalColumns: false,
  })
  const page = resp.recordMap.block[pageId]
  if (!page) return null
  print(page, "\x1b[43m -- page \x1b[0m")
  return {
    name: (page.value as any)?.properties.title[0][0] || "undefined",
    id: pageId,
    lastEditedTime: page.value.last_edited_time || Date.now(),
    icon: page.value?.format?.page_icon || null,
  }
}

/** Refresh the properties of the database  */
export async function refreshCollection(
  client: DummyNotionClient,
  collection: Collection,
  daps: Dap[],
  config: {
    getTemplatesInfo?: boolean
    checkTemplateUpToDate?: TemplateInfo
  }
): Promise<RefreshCollectionResult> {
  let result: RefreshCollectionResult = {
    needToResetCollection: false,
    fieldsToModify: [],
  }

  const resp = await client.agent.loadPageChunk({
    pageId: collection.parent_id,
    limit: 1,
    cursor: {
      stack: [[{ id: collection.parent_id, table: "block", index: 0 }]],
    },
    chunkNumber: 0,
    verticalColumns: false,
  })
  if (!(collection.id in resp.recordMap.collection)) {
    result.needToResetCollection = true
    return result
  }

  result.collection = resp.recordMap.collection[collection.id].value
  let col: any = result.collection
  if (config.getTemplatesInfo && col.template_pages) {
    col.templatesInfo = await Promise.all(
      col.template_pages.map((id) => getTemplateInfo(client, id))
    )
  }

  if (config.checkTemplateUpToDate) {
    const stillExist = (col.template_pages || []).some(
      (id) => config.checkTemplateUpToDate.id
    )
    if (!stillExist) {
      result.needToRemoveTemplate = true
    } else {
      let newTemplate = await getTemplateInfo(
        client,
        config.checkTemplateUpToDate.id
      )
      print(
        newTemplate.lastEditedTime >
          config.checkTemplateUpToDate.lastEditedTime,
        "checking template is up to date",
        config.checkTemplateUpToDate,
        newTemplate
      )
      if (
        newTemplate.lastEditedTime > config.checkTemplateUpToDate.lastEditedTime
      ) {
        result.needToRefreshTemplate = true
      }
      ;(result.collection as any).template = newTemplate
    }
  }

  let schema = result.collection.schema
  const ncol = await client.getCollectionLocally(
    collection.id,
    collection.parent_id,
    schema
  )
  //Verification of the fields
  for (let dap of daps) {
    if (dap.property) {
      if (extraProperty.some((e) => e == dap.property.id)) {
        continue
      }
      if (!(dap.property.id in ncol.schema)) {
        result.fieldsToModify.push({ key: dap.key, action: "remove" })
      } else {
        //check same type
        let nproperty = ncol.schema[dap.property.id]
        if (dap.property.type != nproperty.type) {
          //need to reset
          result.fieldsToModify.push({
            key: dap.key,
            action: "removeDefaultValue",
          })
        } else {
          if (["select", "multi_select"].includes(dap.property.type)) {
            //check if options need to be changed
            switch (dap.property.type) {
              case "select":
                if (
                  dap.options.defaultSelectId &&
                  (!nproperty.options ||
                    !nproperty.options.some(
                      (opt) => opt.id == dap.options.defaultSelectId
                    ))
                ) {
                  result.fieldsToModify.push({
                    key: dap.key,
                    action: "removeDefaultValue",
                  })
                }
                break
              case "multi_select":
                if (
                  dap.options.defaultSelectIds &&
                  (!nproperty.options ||
                    !dap.options.defaultSelectIds.every((id) =>
                      nproperty.options.some((opt) => opt.id == id)
                    ))
                ) {
                  result.fieldsToModify.push({
                    key: dap.key,
                    action: "removeDefaultValue",
                  })
                }
                break
            }
          }
          //check default value
        }
      }
    }
  }
  return result
}

function encrypt(s) {
  try {
    return window.btoa(s)
  } catch {
    return s
  }
}

function decrypt(s) {
  try {
    return window.atob(s)
  } catch {
    return s
  }
}
async function fetchNotionClient(
  client: DummyNotionClient,
  cookie: string
): Promise<
  | { status: "ok"; client: DummyNotionClient }
  | { status: "error"; errorMsg: ErrorGetClient }
> {
  try {
    print("fetch", client.user)
    await client.fetchUserInfos()
    print("fetchdone", client.user)
  } catch (err) {
    return { status: "error", errorMsg: "cookie_expired" }
  }
  await storage.set(
    NOTION_CLIENT_DATA_KEY,
    encrypt(JSON.stringify(client.user))
  )
  await storage.set(COOKIE_KEY, encrypt(cookie))
  return { status: "ok", client }
}

export type ErrorGetClient =
  | "need_to_login_to_notion"
  | "cookie_expired"
  | "network_issue"
/** get the client and update/refresh the cookie */
export async function getClient(
  onStatusChange?: (status: "fetching_client") => void
): Promise<
  | { status: "error"; errorMsg: ErrorGetClient }
  | { status: "ok"; client: DummyNotionClient }
> {
  let client: DummyNotionClient
  const cookieWrap = await promisify(chrome.cookies.get, {
    url: "https://www.notion.so",
    name: "token_v2",
  })
  if (!cookieWrap) {
    return { status: "error", errorMsg: "need_to_login_to_notion" }
  }
  const cookie: string = cookieWrap.value

  let storedCookie = await storage.get(COOKIE_KEY, null)
  if (storedCookie) {
    storedCookie = decrypt(storedCookie)
  }
  let clientData = await storage.get(NOTION_CLIENT_DATA_KEY, null)
  if (clientData) {
    clientData = JSON.parse(decrypt(clientData))
  }
  client = new DummyNotionClient(cookie)
  if (!storedCookie || storedCookie != cookie || !clientData) {
    onStatusChange && onStatusChange("fetching_client")
    return await fetchNotionClient(client, cookie)
  } else {
    client.setUserInfos(clientData)
  }
  return { status: "ok", client }
}

const cachedCollectionsMap: Record<
  string,
  { count: number; map: Record<string, CollectionPage>; order: string[] }
> = {}
const cachedCollectionsTimeout = {}
export async function getCachedCollectionPages(collectionId: string) {
  if (collectionId in cachedCollectionsMap)
    return cachedCollectionsMap[collectionId].map

  const p = await storage.get(`collection-pages-${collectionId}`, {
    count: 0,
    map: {},
    order: [],
  })
  cachedCollectionsMap[collectionId] = p
  return p.map
}

export async function upsertCachedCollectionPage(
  page: CollectionPage,
  collectionId: string
) {
  const p = await getCachedCollectionPages(collectionId)
  if (!(page.id in p)) {
    if (cachedCollectionsMap[collectionId].count > 300) {
      const oldId = cachedCollectionsMap[collectionId].order.shift()
      delete p[oldId]
      cachedCollectionsMap[collectionId].count--
    }
    cachedCollectionsMap[collectionId].count++
    cachedCollectionsMap[collectionId].order.push(page.id)
  }
  p[page.id] = page
  if (!cachedCollectionsTimeout[collectionId]) {
    cachedCollectionsTimeout[collectionId] = setTimeout(async () => {
      //sort
      let m = cachedCollectionsMap[collectionId].map
      const keys = Object.keys(m).sort((a, b) =>
        m[a].name.toLowerCase() < m[b].name.toLowerCase() ? -1 : 1
      )
      let newMap = {}
      keys.forEach(
        (k) => (newMap[k] = cachedCollectionsMap[collectionId].map[k])
      )
      await storage.set(`collection-pages-${collectionId}`, {
        ...cachedCollectionsMap[collectionId],
        map: newMap,
      })
      cachedCollectionsTimeout[collectionId] = null
    }, 200)
  }
  return p
}
