export const notionColors = {
  default: "rgba(206, 205, 202, 0.5)",
  gray: "rgba(155, 154, 151, 0.4)",
  brown: "rgba(140, 46, 0, 0.2)",
  yellow: "rgba(233, 168, 0, 0.2)",
  green: "rgba(0, 135, 107, 0.2)",
  orange: "rgba(245, 93, 0, 0.2)",
  blue: "rgba(0, 120, 223, 0.2)",
  purple: "rgba(103, 36, 222, 0.2)",
  pink: "rgba(221, 0, 129, 0.2)",
  red: "rgba(255, 0, 26, 0.2)",
}

export function randomNotionColor(): keyof typeof notionColors {
  return Object.keys(notionColors)[
    Math.floor(Math.random() * Object.keys(notionColors).length)
  ] as any
}
export type DapOptions = Partial<DapOptionsWhenData>
type ID = string

export type DapOptionsSelectedDataTypes =
  | "webpageUrl"
  | "webpageTitle"
  | "webpageDescription"
  | "webpageImage"
  | "domainName"
  | "websiteIcon"

export type CustomCSSData = {
  type: "simple"
  css: string
  on: "text" | "href" | string
}

type DapOptionsWhenData = {
  selectedData?: DapOptionsSelectedDataTypes
  customCss?: CustomCSSData
  //if Dap kind is "prefilled" and just a simple text
  defaultValue?: string
  //if Dap kind is "prefilled" and is a select data kind
  defaultSelectId?: string
  defaultSelectIds?: string[]
}
type DapKind = "data" | "prefilled" | "field-to-complete"

export type ImageOptions = {
  type: "original" | "coverLike" | "fullWidthCover"
  size: "normal" | "small" | "large"
}

export type FieldOptions = {
  locked: boolean
  //multiline:boolean
  image?: ImageOptions
}

export type OptionParams = {
  action: "new" | "edit"
  id?: string
  databaseLocked?: boolean
}

export type ModifiedDapProperty = Col.ColumnProperty & { id: string }

export type Dap = {
  kind: DapKind
  property?: ModifiedDapProperty
  key: string
  options: DapOptions
  fieldOptions: FieldOptions
}

export type TemplateInfo = {
  id: ID
  name: string
  lastEditedTime: number
  icon?: string
}

export type FormListItem = { name: string; id: string; favorite?: boolean }
export type FormListData = FormListItem[]
export type FormData = {
  collection: Collection
  daps: Dap[]
  id: ID
  name: string
  spaceId: string

  template?: TemplateInfo
  advancedSettingsIsOpen?: boolean
  savePageContent?: boolean
  //runtime parameters
  needToRefreshTemplate?: boolean
}
