export function promisify<Options, Return>(
  fn: (opts: Options, cb: (x?: Return) => void) => void,
  options: Options
): Promise<Return> {
  return new Promise((accept, cancel) => {
    fn(options, accept)
  })
}

export function call(cb) {
  new Promise((resolve, cancel) => {
    resolve(cb())
  })
}

export function makeid(length) {
  var result = ""
  var characters =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789"
  var charactersLength = characters.length
  for (var i = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength))
  }
  return result
}

export const print =
  process.env.NODE_ENV === "production" ? (...args) => {} : console.log

export const sleep = async (ms) =>
  new Promise((resolve) => setTimeout(resolve, ms))

//dead simple implementation. more robust here: https://stackoverflow.com/questions/27078285/simple-throttle-in-js
export function throttle(fn, waitMs: number) {
  let last: Date
  let prevFn: any
  let prevTimer: any
  return (...args) => {
    const now = new Date()
    if (prevFn) {
      prevFn(undefined)
      clearTimeout(prevTimer)
      prevFn = null
    }
    return new Promise((resolve) => {
      const x = last
        ? Math.max(0, waitMs - (now.getTime() - last.getTime()))
        : 0
      prevFn = resolve
      prevTimer = setTimeout(function () {
        last = now
        prevFn = null
        resolve(fn.apply(null, args))
      }, x)
    })
  }
}

export function getChipColor(option: any) {
  return option.color in notionColors
    ? notionColors[option.color]
    : notionColors.default
}

const arrayMoveMutate = (array, from, to) => {
  const startIndex = to < 0 ? array.length + to : to
  const item = array.splice(from, 1)[0]
  array.splice(startIndex, 0, item)
}

export const arrayMove = (array, from, to) => {
  array = array.slice()
  arrayMoveMutate(array, from, to)
  return array
}
