function getInfoFromCurrentPage(): Promise<{
  url: string
  title: string
} | null> {
  return new Promise((resolve) => {
    chrome.tabs.query({ active: true, currentWindow: true }, function (tabs) {
      if (tabs.length == 0) {
        resolve(undefined)
        return
      }
      resolve({ title: tabs[0].title, url: tabs[0].url })
    })
  })
}

