const ChooseFormPage = ({
  forms,
  onSelectForm,
  metadatas,
  canSavePage,
  onChangeForms,
  connectedToNotion,
}: {
  forms: FormListItem[]
  metadatas
  onSelectForm
  canSavePage: boolean
  onChangeForms: (forms: FormListItem[]) => void
  connectedToNotion: boolean
}) => {
  const { openOptionsPage } = React.useContext(DataContext)
  if (!connectedToNotion) {
    return (
      <TopLayout
        metadatas={metadatas}
        layoutProps={{ rightIcon: "info" }}
        noMargin
      >
        <Boxfc align="center" mb={1}>
          <H4>Hello  </H4>
          <H4 mb={3}> To save this page, please log in to Notion</H4>
          <Button
            onClick={() => {
              chrome.tabs.create({ url: `https://www.notion.so/login` })
              window.close()
            }}
          >
            Log in to Notion
          </Button>
        </Boxfc>
      </TopLayout>
    )
  }

  return (
    <TopLayout
      metadatas={metadatas}
      layoutProps={{ rightIcon: "info" }}
      noMargin
    >
      <Boxfc pl={"6px"}>
        <FormList
          forms={forms}
          useDragHandle={true}
          canSavePage={canSavePage}
          onSortEnd={({ oldIndex, newIndex }) => {
            document.body.style.cursor = "default"
            oldIndex != newIndex &&
              onChangeForms(arrayMove(forms, oldIndex, newIndex))
          }}
          onSelectForm={onSelectForm}
          onClickRemoveFormItem={(formId) => {
            onChangeForms(forms.filter((f) => f.id != formId))
          }}
          onDuplicateItem={async (formInfo) => {
            const fullForm = await getFormData(formInfo.id)
            const newFormInfo = await saveFormData(
              { ...fullForm, name: `${formInfo.name} (Copy)` },
              "new",
              null
            )
            onChangeForms([...forms, newFormInfo])
          }}
          onToggleFavoriteForm={(formId) => {
            onChangeForms(
              forms.map((f) => ({
                ...f,
                favorite: f.id == formId ? (f.favorite ? false : true) : false,
              }))
            )
          }}
          onSortStart={() => (document.body.style.cursor = "grabbing")}
          lockAxis="y"
          lockToContainerEdges={true}
          distance={5}
        />
      </Boxfc>
      {forms.length == 0 ? (
        <>
          <Boxfc ml={3.5}>
            <Button
              //variant="text"
              onClick={() => openOptionsPage({ action: "new" })}
            >
              Create a form
            </Button>
          </Boxfc>
        </>
      ) : (
        <Box ml={3.5}>
          <Boxf
            className="addFormButton"
            align="center"
            component="button"
            onClick={() => openOptionsPage({ action: "new" })}
            style={{}}
          >
            <PlusIcon />
            New Form
          </Boxf>
        </Box>
      )}
    </TopLayout>
  )
}

async function loadCustomCss(form: FormData): Promise<FormData> {
  let customs = []
  form.daps.forEach(
    (e) =>
      e.options.customCss &&
      customs.push({ key: e.key, ...e.options.customCss })
  )
  if (!customs.length) return form
  var port = chrome.runtime.connect({ name: "get_custom_css" })

  return new Promise((resolve) => {
    port.postMessage({ type: "initial_data", params: { customs } })
    port.onMessage.addListener((x) => {
      switch (x.type) {
        case "error":
          print("CUSTOM CSS ERROR!")
          port.disconnect()
          resolve(form)
          break
        case "done":
          let md = {}
          let i = 0
          for (let c of customs) {
            md[c.key] = x.data[i]
            i++
          }
          let daps = form.daps.map((e) => {
            if (!(e.key in md)) return e
            return { ...e, options: { defaultValue: md[e.key] } }
          })
          print("dasp", daps, md)
          resolve({ ...form, daps })
      }
    })
  })
}

async function getCurrentPageMetadatas() {
  const p = await getInfoFromCurrentPage()
  if (!p || !p.url?.startsWith("http") || p.url?.includes("chrome.google.com"))
    return null
  return new Promise((done) => {
    var port = chrome.runtime.connect({ name: "get_metadatas" })
    port.onDisconnect.addListener(() => {
      done(null)
      console.log("disconnected")
    })
    port.onMessage.addListener((x) => {
      console.log("received", x)
      switch (x.type) {
        case "done":
          done(x.data)
          port.disconnect()
          break
        case "error":
          done(null)
      }
    })
  })
}

