function InfoDialog(props) {
  const { onClose, selectedValue, open } = props
  return (
    <Dialog fullScreen onClose={onClose} open={open}>
      <Box style={{ position: "absolute", right: 10, top: 5 }}>
        <>
          <IconButton onClick={onClose}>
            <CloseIcon size="small" />
          </IconButton>
        </>
      </Box>
      <Boxfc height="100%" justify="center">
        <InfoHeader />
      </Boxfc>
    </Dialog>
  )
}

const LayoutHeader = ({
  onClickOpenOption,
  onClickGoBack,
  canGoBack,
  prevPageName,
  rightIcon,
  hideAppTitle,
}: {
  onClickOpenOption
  onClickGoBack
  canGoBack: boolean
  prevPageName: string
  rightIcon: RightIcon
  hideAppTitle?: boolean
}) => {
  return (
    <>
      <Box p={0.5} pt={1} mb={1} height="48px">
        {!canGoBack ? (
          <>
            {(hideAppTitle == true && <></>) || (
              <Boxf justify="center" pt={1.3} align="center">
                <H4>{APP_NAME} </H4>
              </Boxf>
            )}
          </>
        ) : (
          <>
            <Box pt={0.8} pl={2}>
              <Button
                variant="text"
                onClick={onClickGoBack}
                startIcon={canGoBack && <KeyboardArrowLeftIcon />}
                disabled={!canGoBack}
              >
                {prevPageName}
              </Button>
            </Box>
          </>
        )}
      </Box>
      <Box style={{ position: "absolute", right: 10, top: 5 }}>
        {rightIcon == "info" ? (
          <Opener>
            {(modal) => (
              <>
                <IconButton onClick={modal.handleOpen}>
                  <InfoIcon size="small" />
                </IconButton>
                <InfoDialog open={modal.isOpen} onClose={modal.handleClose} />
              </>
            )}
          </Opener>
        ) : rightIcon == "settings" ? (
          <IconButton onClick={onClickOpenOption}>
            <SettingsIcon size="small" />
          </IconButton>
        ) : (
          <></>
        )}
      </Box>
    </>
  )
}
type RightIcon = "settings" | "info" | "none"
export type LayoutProps = {
  canGoBack?: boolean
  onClickGoBack?: () => void
  prevPageName?: string
  rightIcon?: RightIcon
  optionParams?: OptionParams
  minWidth?: string
  minHeight?: string
  hideAppTitle?: boolean
}

export const Layout = ({
  children,
  canGoBack = false,
  optionParams = null,
  onClickGoBack = null,
  prevPageName = "",
  minWidth,
  minHeight,
  rightIcon,
  hideAppTitle,
}: LayoutProps & { children: any }) => {
  const { openOptionsPage } = React.useContext(DataContext)
  return (
    <Boxfc minWidth={minWidth || "380px"} minHeight={minHeight} pb={2}>
      <LayoutHeader
        hideAppTitle={hideAppTitle}
        prevPageName={prevPageName}
        onClickOpenOption={() => openOptionsPage(optionParams)}
        onClickGoBack={onClickGoBack}
        canGoBack={canGoBack}
        rightIcon={rightIcon || "none"}
      />
      <Boxfc flex={1} justify="center">
        {children}
      </Boxfc>
    </Boxfc>
  )
}
