import { promisify, print } from "../utils"

function getCacheKey(key, i) {
  return i === 0 ? key : key + "_" + i
}

const QUOTA_BYTES_PER_ITEM = chrome.storage.sync?.QUOTA_BYTES_PER_ITEM || 8192
const MAX_ITEMS = chrome.storage.sync?.MAX_ITEMS || 512

/**
 * Allows to save strings longer than QUOTA_BYTES_PER_ITEM in chrome.storage.sync by splitting them into smaller parts.
 * Please note that you still can't save more than QUOTA_BYTES.
 *
 * @param {string} key
 * @param {string} value
 * @param {function(): void=} callback
 */
function xset(key, obj, callback) {
  var i = 0,
    cache = {},
    segment,
    cacheKey

  // split value into chunks and store them in an object indexed by `key_i`
  let value = JSON.stringify(obj)
  let x: any[] = []
  while (value.length > 0) {
    cacheKey = getCacheKey(key, i)
    //if you are wondering about -2 at the end see: https://code.google.com/p/chromium/issues/detail?id=261572
    segment = value.substr(0, QUOTA_BYTES_PER_ITEM - cacheKey.length - 2)
    x.push(segment.length)
    cache[cacheKey] = segment
    value = value.substr(QUOTA_BYTES_PER_ITEM - cacheKey.length - 2)
    i++
  }
  //console.log(chrome.storage.sync.QUOTA_BYTES_PER_ITEM, Object.keys(cache), x);

  // store all the chunks
  chrome.storage.local.set(cache, callback)

  //we need to make sure that after the last chunk we have an empty chunk. Why this is so important?
  // Saving v1 of our object. Chrome sync status: [chunk1v1] [chunk2v1] [chunk3v1]
  // Saving v2 of our object (a bit smaller). Chrome sync status: [chunk1v2] [chunk2v2] [chunk3v1]
  // When reading this configuration back we will end up with chunk3v1 being appended to the chunk1v2+chunk2v2
  chrome.storage.local.remove(getCacheKey(key, i))
}

/**
 * Retrieves chunks of value stored in chrome.storage.sync and combines them.
 *
 * @param {string} key
 * @param {function(string):void=} callback
 * @param {function(string):void=} error callback
 */
function xget(key, cb, err) {
  //get everything from storage
  chrome.storage.local.get(null, function (items) {
    var i,
      value = ""
    let found = false
    for (i = 0; i < MAX_ITEMS; i++) {
      if (items[getCacheKey(key, i)] === undefined) {
        break
      }
      found = true
      value += items[getCacheKey(key, i)]
    }
    //console.log("value",value);
    if (found) cb(JSON.parse(value))
    else err()
  })
}

//let v = {}

export async function get(key, defaultValue): Promise<any> {
  //remove
  /*return new Promise((accept) => {
    print("set", key, defaultValue)
    if (v[key]) accept(v[key])
    else accept(defaultValue)
  })*/
  //old
  return new Promise((accept) => {
    xget(
      key,
      (value) => {
        accept(value)
      },
      async () => {
        print("no value found, saving default")
        await set(key, defaultValue)
        accept(defaultValue)
      }
    )
  })
}

export async function set(key, obj: any): Promise<void> {
  /*print(
    "storing",
    key,
    obj,
    chrome.storage.sync.QUOTA_BYTES_PER_ITEM,
    chrome.storage.sync.MAX_ITEMS
  )*/
  new Promise((accept) => {
    xset(key, obj, () => {
      accept()
    })
  })
}

export default {
  set,
  get,
}
