import React from "react"
import {
  SelectIcon,
  CheckboxIcon,
  MultiSelectIcon,
  TextIcon,
  TitleIcon,
  LinkIcon,
  TemplateIcon,
  RelationIcon,
  NumberIcon,
} from "../shared/icons"
const ImageIcon = require("@material-ui/icons/Image").default
const AssignmentOutlinedIcon = require("@material-ui/icons/AssignmentOutlined")
  .default

const dataTypeIcon = {
  select: <SelectIcon />,
  multi_select: <MultiSelectIcon />,
  checkbox: <CheckboxIcon />,
  text: <TextIcon />,
  url: <LinkIcon />,
  title: <TitleIcon />,
  relation: <RelationIcon />,
  number: <NumberIcon />,
  image: (
    <ImageIcon
      style={{
        fontSize: "16px",
        color: "rgba(55, 53, 47, 0.4)",
        paddingTop: "3px",
      }}
    />
  ),
  template: <TemplateIcon />,
  saveContent: (
    <AssignmentOutlinedIcon
      style={{
        fontSize: "18px",
        color: "rgba(55, 53, 47, 0.4)",
        paddingTop: "3px",
      }}
    />
  ),
}

export const PropretyName = ({ option }: { option: any }) => {
  return (
    <div style={{ display: "flex", alignItems: "center" }}>
      <div style={{ width: 25 }}>
        {option.type in dataTypeIcon && (
          <div style={{ marginRight: "4px" }}>{dataTypeIcon[option.type]}</div>
        )}{" "}
      </div>
      <div style={{ marginLeft: "4px", fontSize: "14px" }}>{option.name}</div>
    </div>
  )
}
