const CircularProgress = require("@material-ui/core/CircularProgress").default

export const Loading = ({
  msg = null,
  height = "300px",
  minLayoutHeight,
}: {
  msg?: string
  height?: string
  minLayoutHeight?: string
}) => {
  return (
    <Layout minHeight={minLayoutHeight}>
      <Boxfc justify="center" align="center" height={height} px={2} py={0.5}>
        <Box mb={1}>
          <CircularProgress color="primary" size={20} />
        </Box>
        <H4>{msg}</H4>
      </Boxfc>
    </Layout>
  )
}

export const Error = ({ msg }: { msg?: string }) => {
  return (
    <Layout>
      <Boxfc justify="center" align="center">
        <H3 mb={1} color="red">
          Error
        </H3>
        <H4>{msg}</H4>
      </Boxfc>
    </Layout>
  )
}
