const CircularProgress = require("@material-ui/core/CircularProgress").default
const ReplayIcon = require("@material-ui/icons/Replay").default

//let last:Date;
//let deferTimer: ReturnType<typeof setTimeout>;

const toNotionImageUrl = (url: string) => {
  return `https://notion.so/image/${encodeURIComponent(
    url.startsWith("/image") ? `https://notion.so${url}` : url
  )}`
}

const toCollectionImageUrl = (
  url: string,
  collectionId: string,
  userId: string
) => {
  return `${toNotionImageUrl(
    url
  )}?table=collection&id=${collectionId}&userId=${userId}&cache=v2`
}

const ColIcon = ({
  icon,
  collectionId,
  userId,
}: {
  icon: string | null
  collectionId: string
  userId: string
}) => {
  if (!icon)
    return (
      <div style={{ width: "27px" }}>
        <NotionPageIcon />
      </div>
    )
  return (
    <div style={{ marginRight: "4px", display: "flex", alignItems: "center" }}>
      {icon.startsWith("http") ? (
        <>
          {
            <img
              src={toCollectionImageUrl(icon, collectionId, userId)}
              height="20px"
              width="20px"
            />
          }
        </>
      ) : (
        <>{icon}</>
      )}
    </div>
  )
}
const Parents = ({ parents }: { parents: { name: string }[] }) => {
  return (
    <div style={{ color: "grey", fontSize: 11, fontWeight: 350 }}>
      {parents.length >= 1 && <>{parents[0].name}</>}
      {parents.length >= 3 ? ` / ... / ` : parents.length == 2 ? ` / ` : ``}
      {parents.length >= 2 && <>{parents[parents.length - 1].name}</>}
    </div>
  )
}

const CollectionSelect = ({
  collection,
  onSelectCollection,
  spaceId,
  daps,
  disabled,
}: {
  collection: Collection | null
  spaceId: string
  daps: Dap[]
  disabled?: boolean
  onSelectCollection: (col: Collection) => void
}) => {
  const [open, setOpen] = React.useState(false)
  const [active, setActive] = useState(false)
  const [focus, setFocus] = useState(false)
  const [collections, setCollections] = useState({ count: 0, collections: {} })
  const [options, setOptions] = React.useState<Collection[]>([
    ...(collection ? [collection] : []),
  ])
  const [inputValue, setInputValue] = useState("")
  const loading = open && active
  const { client } = useContext(DataContext)

  const updateOptions = (fetchedCollections: Collection[]) => {
    let ids = {}
    let opts = [...fetchedCollections]
    fetchedCollections.forEach((e) => {
      ids[e.id] = true
    })
    //print("got", ids)
    for (let i = 0; i < options.length && i < 70; i++) {
      if (!(options[i].id in ids)) {
        opts.push(options[i])
      }
    }
    setOptions(opts)
  }

  const searchCollections: (v: string) => Promise<Collection[]> = useMemo(
    () =>
      throttle(async function (v) {
        const x = await client.searchCollections(v, spaceId)
        return x
      }, 500),
    [spaceId]
  ) as any

  useEffect(() => {
    if (collection) {
      setInputValue(collection.name[0][0])
    }
  }, [collection])
  useEffect(() => {
    setFocus(false)
    setActive(true)
    ;(async () => {
      //print(`calling... '${inputValue}'`, )
      const x: Collection[] | undefined = await searchCollections(inputValue)
      //print(`result... '${inputValue}'`, x)
      if (x) {
        updateOptions(x)
        setActive(false)
      }
    })()
  }, [inputValue, focus])

  return (
    <>
      <Boxf align="center" flex={1}>
        <Box
          style={{
            minWidth: "200px",
            marginRight: "28px",
            fontSize: "1rem",
            fontWeight: 400,
          }}
        >
          <PropretyName option={{ name: "Database", type: "database" }} />
        </Box>
        <Boxf flex={1}>
          <Autocomplete
            size="small"
            disabled={disabled}
            style={{ width: 220 }}
            inputValue={inputValue}
            onInputChange={(ev, value) => setInputValue(value)}
            value={collection}
            open={open}
            onOpen={() => {
              setOpen(true)
            }}
            loading={loading}
            openOnFocus={true}
            onFocus={() => {
              setFocus(true)
            }}
            onClose={() => {
              setOpen(false)
            }}
            onChange={(
              ev,
              value,
              reason:
                | "create-option"
                | "select-option"
                | "remove-option"
                | "blur"
                | "clear"
            ) => {
              console.log("select database", value, reason)
              onSelectCollection && onSelectCollection(value)
            }}
            getOptionSelected={(option, value) =>
              option.name[0][0] === value.name[0][0]
            }
            renderOption={(option) => {
              //console.log("got", option)
              return (
                <>
                  <div
                    style={{
                      display: "flex",
                      alignItems: "center",
                    }}
                  >
                    <ColIcon
                      icon={option.icon}
                      collectionId={option.id}
                      userId={client.user.id}
                    />
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "column",
                      }}
                    >
                      <div>{option.name[0][0]}</div>
                      <Parents parents={option.parents || []} />
                    </div>
                  </div>
                </>
              )
            }}
            getOptionLabel={(option) => option.name[0][0]}
            options={options}
            renderInput={(params) => (
              <TextField
                {...params}
                style={{ width: "200px" }}
                className="collectionField"
                placeholder="Select Database..."
                InputProps={{
                  ...params.InputProps,
                  endAdornment: (
                    <React.Fragment>
                      {loading ? (
                        <CircularProgress color="inherit" size={20} />
                      ) : null}
                      {params.InputProps.endAdornment}
                    </React.Fragment>
                  ),
                }}
              />
            )}
          />
        </Boxf>
      </Boxf>
      <Boxf align="center">
        <Boxfc width="300px"></Boxfc>
        {/*<Box pl={1}>
          <Button
            onClick={() => {
              handleRefreshCollection()
            }}
            endIcon={<ReplayIcon />}
            disabled={!collection}
            variant="text"
          >
            Refresh Database
          </Button>
        </Box>*/}
      </Boxf>
    </>
  )
}

export default CollectionSelect
