const TypeTextField = ({
  defaultValue,
  onChangeValue,
  refresh,
  name,
  textClass,
  textStyle,
  isNumber,
}: {
  defaultValue: string
  onChangeValue: (v: string) => void
  refresh?: boolean
  name: string
  textClass?: string
  textStyle?: any
  isNumber?: boolean
}) => {
  const [value, setValue] = useState(defaultValue)
  const saveTimeout = React.useRef(null)

  useEffect(() => {
    setValue(defaultValue)
  }, [defaultValue])
  return (
    <TextField
      className={textClass || undefined}
      style={textStyle || {}}
      value={value}
      multiline
      rowsMax={3}
      onChange={(ev) => {
        setValue(ev.target.value)
        if (!saveTimeout.current) {
          saveTimeout.current = setTimeout(() => {
            setValue((v) => {
              onChangeValue(v)
              saveTimeout.current = null
              return v
            })
          }, 700)
        }
      }}
      placeholder={`${name}...`}
      onBlur={() => {
        print("got blurred", value)
        onChangeValue(value)
      }}
    />
  )
}

