export const TextFieldWithData = ({
  datas,
  name,
  textClass,
  options,
  onSetOptions,
  textStyle,
}: {
  datas: DataPage[]
  options: DapOptions
  onSetOptions: (opt: DapOptions) => void
  name: string
  textClass?: string
  textStyle?: any
}) => {
  const [value, setValue] = useState(
    options.selectedData
      ? [datas.find((d) => d.key == options.selectedData)]
      : /*options.defaultValue
      ? [options.defaultValue]
      :*/ []
  )
  const [inputValue, setInputValue] = useState(options.defaultValue || "")
  //print("state:", options, inputValue, inputValue.length, value)

  useEffect(() => {
    if (options.defaultValue) setInputValue(options.defaultValue)
    else setInputValue("")
  }, [options])
  return (
    <>
      <Autocomplete
        multiple
        freeSolo
        size="small"
        includeInputInList
        inputValue={inputValue}
        onInputChange={(ev, v, reason: "input" | "reset" | "clear") =>
          setInputValue(v)
        }
        blurOnSelect
        value={value}
        options={datas || []}
        getOptionLabel={(option) => {
          return option.name
        }}
        renderOption={(option: DataPage) => {
          return <DataOptionRow data={option} />
        }}
        renderTags={(tagValue, getTagProps) => {
          return tagValue.map((opt, index) => {
            console.log(">>>>", opt)
            if (typeof opt == "string") return opt
            const { key, ...tagProps } = getTagProps({ index })
            return <DataSelectedOption data={opt} />
          })
        }}
        filterOptions={(options, params) => {
          const filtered = filter(options, params)
          return filtered
        }}
        style={{}}
        onChange={async (
          ev,
          options,
          reason:
            | "create-option"
            | "select-option"
            | "remove-option"
            | "blur"
            | "clear"
        ) => {
          //check need to create an option
          let opts = options
          if (reason == "blur") {
            print("BLUR")
          }
          print("SUBMIT", opts, reason, "\x1b[43m -- opts \x1b[0m")
          if (opts.length == 0) {
            onSetOptions({ defaultValue: "" })
            setValue([])
            return
          }
          //onSetOptions({ defaultSelectValue: opts[opts.length - 1].id })
          let opt = opts[0]
          if (typeof opt == "string") {
            print("save here", opt)
            onSetOptions({ defaultValue: opt })
            //setValue([])
            //setInputValue(opt)
            //setValue([opt])
          } else {
            onSetOptions({ selectedData: opt.key })
            setValue([opt])
          }
        }}
        renderInput={(params) => {
          return (
            <TextField
              onMouseDown={params.inputProps.onMouseDown}
              className={textClass || undefined}
              placeholder={value.length == 0 ? "Empty" : ""}
              style={textStyle || {}}
              {...params}
              variant="outlined"
            />
          )
        }}
      />
    </>
  )
}
