function getDefaultOptionOfSelect(dap: Dap) {
  if (!dap.options.defaultSelectId || !dap.property.options) return undefined
  let x = dap.property.options.find(
    (opt) => opt.id == dap.options.defaultSelectId
  )
  return x
}

export const SelectFieldNoAutoComplete = ({
  value,
  options,
  onSelectOption,
  renderOption,
  uniqueKey,
  style,
  className,
  renderValue,
  disabled,
  onOpen,
  displayEmpty,
  renderOptionEmpty,
  renderValueEmpty,
}: {
  value
  className?: string
  style?: {}
  options
  onSelectOption
  renderOption
  uniqueKey: string
  renderValue
  disabled?: boolean
  onOpen?: any
  displayEmpty?: boolean
  renderOptionEmpty?: () => any
  renderValueEmpty?: () => any
}) => {
  return (
    <>
      <Select
        disabled={disabled}
        style={style}
        className={className}
        value={value}
        onOpen={onOpen}
        onChange={(ev) => {
          onSelectOption(ev.target.value)
        }}
        renderValue={(id) => {
          if (renderValueEmpty && id == "") return renderValueEmpty()
          return renderValue(id)
        }}
        displayEmpty={displayEmpty}
      >
        {renderOptionEmpty && (
          <MenuItem value="">{renderOptionEmpty()}</MenuItem>
        )}
        {options.map((opt) => (
          <MenuItem key={opt[uniqueKey]} value={opt[uniqueKey]}>
            {renderOption(opt)}
          </MenuItem>
        ))}
      </Select>
    </>
  )
}

