function getDefaultOptionsOfMultiSelect(dap: Dap) {
  if (!dap.options.defaultSelectIds || !dap.property.options) return []
  let ids = {}
  dap.options.defaultSelectIds.forEach((id) => (ids[id] = true))
  let xs = dap.property.options.filter((opt) => ids[opt.id])
  return xs
}
function pass(str: string) {
  return str.replace(/,/g, " ")
}

export const MultiSelectField = ({
  dap,
  onChangeField,
  refresh,
  textClass,
  textStyle,
  size,
}: {
  dap: Dap
  onChangeField: (field: Dap) => void
  refresh?: boolean
  textClass?: string
  textStyle?: any
  size?: "normal" | "small"
}) => {
  const elRef = React.useRef(null)
  const [value, setValue] = useState(getDefaultOptionsOfMultiSelect(dap))
  const { createNewValueForProperty, refreshCollection } = useContext(
    FormSubmissionContext
  )
  const [focus, setFocus] = useState(false)

  useEffect(() => {
    const x = getDefaultOptionsOfMultiSelect(dap)
    print("optimistic, rerender here", refresh, dap, value, x)
    setValue(x)
  }, [dap])

  return (
    <>
      <Autocomplete
        multiple
        value={value}
        size={size}
        openOnFocus
        disableCloseOnSelect={true}
        options={dap.property.options || []}
        getOptionLabel={(option) => option.value}
        style={{}}
        renderOption={(option) => {
          if (option.type == "create")
            return (
              <div>
                <span style={{ marginRight: "2px" }}>Create</span>
                <Chip
                  label={option.value}
                  style={{ backgroundColor: getChipColor(option) }}
                />
              </div>
            )
          return (
            <Chip
              label={option.value}
              style={{ backgroundColor: getChipColor(option) }}
            />
          )
        }}
        onChange={(
          ev,
          options,
          reason:
            | "create-option"
            | "select-option"
            | "remove-option"
            | "blur"
            | "clear"
        ) => {
          let newOpt = null
          const opts: any[] = options.map((opt) => {
            if (opt.type == "create") {
              newOpt = createNewValueForProperty(
                dap.property.id,
                opt.value,
                opt.color
              )
              return newOpt
            }
            return opt
          })
          setValue(opts)
          //optimistically update field (add new option before sync on server)
          onChangeField({
            ...dap,
            options: {
              defaultSelectIds: opts.map((opt) => opt.id),
            },
            property: {
              ...dap.property,
              options: [
                ...(dap.property.options || []),
                ...(newOpt ? [newOpt] : []),
              ],
            },
          })
        }}
        filterOptions={(options, params) => {
          const filtered = filter(options, params)

          // Suggest the creation of a new value
          if (
            params.inputValue !== "" &&
            !(filtered.length == 1 && filtered[0].value == params.inputValue)
          ) {
            filtered.push({
              type: "create",
              value: pass(params.inputValue),
              color: randomNotionColor(),
            })
          }

          return filtered
        }}
        limitTags={3}
        renderTags={(tagValue, getTagProps) =>
          tagValue.map((option, index) => {
            const { key, ...tagProps } = getTagProps({ index })
            return (
              <Chip
                key={key}
                data-tag-index={tagProps["data-tag-index"]}
                label={option.value}
                style={{
                  backgroundColor: getChipColor(option),
                  marginLeft: "2px",
                  marginTop: "2px",
                }}
              />
            )
          })
        }
        renderInput={(params) => (
          <TextField
            onMouseDown={(ev) => {
              if ([...(ev.target?.classList || [])].includes("MuiSvgIcon-root"))
                return
              return params.inputProps.onMouseDown(ev)
            }}
            /*
            //inputRef={elRef}
            onMouseDown={(...args) => {
              if (focus == true) {
                print("BLUR!!", elRef, params)
                elRef.current.blur()
                setFocus(false)
                params.inputProps.ref.current.blur()
              }
              params.inputProps.onMouseDown(...args)
            }}*/
            placeholder={value.length == 0 ? "Empty" : ""}
            className={textClass || undefined}
            style={textStyle || {}}
            {...params}
            /*inputProps={{
              ...params.inputProps,
              onFocus: (...args) => {
                setFocus(true)
                print("focus")
                params.inputProps.onFocus(...args)
              },
              onBlur: (...args) => {
                setFocus(false)
                print("blur")
                params.inputProps.onBlur(...args)
              },
            }}*/
            variant="outlined"
          />
        )}
      />
    </>
  )
}

function getDefaultOptionsOfRelation(
  field: Dap,
  map: Record<string, CollectionPage>
) {
  if (!field.options.defaultSelectIds) return []
  let xs = []
  field.options.defaultSelectIds.forEach((id) => {
    xs.push(map[id])
  })
  return xs
}

const RelationOption = ({ name, icon }: { name: string; icon: string }) => {
  return <div>{name}</div>
}

const toNotionImageUrl = (url: string) => {
  return `https://notion.so/image/${encodeURIComponent(
    url.startsWith("/image") ? `https://notion.so${url}` : url
  )}`
}

const toPageImageUrl = (url: string, pageId: string, userId: string) => {
  return `${toNotionImageUrl(
    url
  )}?table=block&id=${pageId}&userId=${userId}&cache=v2`
}

const PageIcon = ({
  icon,
  pageId,
  userId,
}: {
  icon: string | null
  pageId: string
  userId: string
}) => {
  if (!icon)
    return (
      <div style={{ width: "27px" }}>
        <NotionPageIcon />
      </div>
    )
  return (
    <div style={{ marginRight: "4px", display: "flex", alignItems: "center" }}>
      {icon.startsWith("http") ? (
        <>
          {
            <img
              src={toPageImageUrl(icon, pageId, userId)}
              height="20px"
              width="20px"
            />
          }
        </>
      ) : (
        <>{icon}</>
      )}
    </div>
  )
}

