export const Field = ({
  onChangeOptions,
  onChangeField,
  dap,
  metadatas,
  refresh,
}: {
  onChangeOptions: (options: any) => void
  onChangeField: (field: Dap) => void
  dap: Dap
  metadatas
  refresh: boolean
}) => {
  if (dap.property.type == ("image" as any)) {
    return null
  }
  if (dap.fieldOptions.locked == true) return null
  /* return <Boxf color="text.secondary" maxWidth="300px" align="center">
      <FieldWrapper label={dap.property.name} propertyType={dap.property.type}><></></FieldWrapper>
      <Txt ml={1}></Txt>
      <Box  ml={1} style={{fontStyle:'italic',
    "overflow":"hidden","whiteSpace":"nowrap","textOverflow":"ellipsis","wordBreak":"break-all","wordWrap":"break-word",
    fontSize:'11px',
    }}>
    <CustomDisplayDataValue dap={dap}/>
    </Box>
    </Boxf>
    */
  if (dap.property.type == "checkbox") {
    return (
      <Box mb={1}>
        <TypeCheckboxField
          refresh={refresh}
          defaultValue={
            (typeof dap.options?.defaultValue == "boolean" &&
              dap.options?.defaultValue) ||
            false
          }
          propertyName={dap.property.name || "empty"}
          onChangeValue={(defaultValue) => onChangeOptions({ defaultValue })}
        />
      </Box>
    )
  }
  return (
    <Box mb={1}>
      <FieldWrapper label={dap.property.name} propertyType={dap.property.type}>
        {() => {
          switch (dap.property.type) {
            case "url":
            case "title":
            case "text":
            case "number":
              return (
                <TypeTextField
                  isNumber={dap.property.type == "number"}
                  defaultValue={dap.options.defaultValue}
                  onChangeValue={(defaultValue) =>
                    onChangeOptions({ defaultValue })
                  }
                  textClass="fieldText"
                  name={dap.property?.name || "Untitled"}
                />
              )
            case "select":
              return (
                <SelectField
                  dap={dap}
                  textClass="fieldSelect"
                  onChangeField={onChangeField}
                />
              )
            case "multi_select":
              return (
                <MultiSelectField
                  onChangeField={onChangeField}
                  textClass="fieldSelect"
                  dap={dap}
                />
              )
            case "relation":
              return (
                <RelationField
                  onChangeOptions={onChangeOptions}
                  textClass="fieldSelect"
                  dap={dap}
                />
              )

            default:
              return <>property's type not implemented</>
          }
        }}
      </FieldWrapper>
    </Box>
  )
}
