async function loadImage(
  url
): Promise<{ imgUrl: string; width: number; height: number } | null> {
  var img = new Image()
  return new Promise((resolve) => {
    img.addEventListener("load", function () {
      resolve({
        imgUrl: url,
        width: this.naturalWidth,
        height: this.naturalHeight,
      })
    })
    img.src = url
  })
}

const SuccessView = ({ rowId }: { rowId: string }) => {
  const [openInDesktop, setOpenInDesktop] = useState(false)
  function openInNotion() {
    chrome.tabs.create({
      url: `${openInDesktop ? "notion://" : "https://"}notion.so/${rowId}`,
    })
    window.close()
  }

  function onKeyUp(e) {
    if (e.code == "Enter") {
      openInNotion()
    }
  }
  useEffect(() => {
    ;(async () => {
      setOpenInDesktop(await storage.get("open-desktop", false))
    })()
  }, [])

  useEffect(() => {
    document.addEventListener("keyup", onKeyUp)

    return () => {
      document.removeEventListener("keyup", onKeyUp)
    }
  }, [])

  return (
    <>
      <Layout hideAppTitle={false}>
        <Boxfc align="center" justify="center" px={2} py={0.5}>
          <H3 mb={3} mt={7}>
            Saved to Notion 
          </H3>
          <Boxfc justify="center" px={1} pb={2}>
            <CheckBoxField
              checkboxClass="checkBoxField"
              name="open in desktop app"
              onChange={(v) => {
                storage.set("open-desktop", v)
                setOpenInDesktop(v)
              }}
              value={openInDesktop}
              size="small"
            />
          </Boxfc>

          <Boxfc width="100%">
            <Button onClick={() => openInNotion()}>open in Notion</Button>
          </Boxfc>
          <Boxfc mt={2} width="100%">
            <Button onClick={() => window.close()} variant="outlined">
              close popup
            </Button>
          </Boxfc>
        </Boxfc>
      </Layout>
    </>
  )
}

