function getFrontImage(fields: Dap[]) {
  const images = fields.flatMap(
    (v) =>
      ((v.property.type as any) == "image" &&
        (v.options?.defaultValue as any)?.imgUrl && [v]) ||
      []
  )
  console.log("images", images)
  images.sort((a, b) => {
    if (a.property?.id == "pageFrontImage") return -1
    if (b.property?.id == "pageFrontImage") return 1
    return 0
  })
  console.log("sorted images", images)
  return images[0]
}

function getQueryParamsFromUrl(url) {
  var regex = /[?&]([^=#]+)=([^&#]*)/g,
    params = {},
    match
  while ((match = regex.exec(url))) {
    params[match[1]] = match[2]
  }
  return params
}

function getImage(url: string, image: string | null) {
  if (url.startsWith("https://www.youtube.com")) {
    let params = getQueryParamsFromUrl(url)
    if (image?.includes(params["v"])) return image
    if ("v" in params) {
      return `https://i.ytimg.com/vi/${params["v"]}/maxresdefault.jpg`
    }
  }
  return image
}

export function getmetadata(
  selectedData: DapOptionsSelectedDataTypes,
  metadatas
) {
  switch (selectedData) {
    case "webpageTitle":
      return metadatas["title"]
    case "webpageUrl":
      return metadatas["url"]
    case "domainName":
      return metadatas["domainName"]
    case "webpageDescription":
      return metadatas["description"]
    case "webpageImage":
      return {
        imgUrl: getImage(metadatas.url, metadatas["image"]),
        width: null,
        height: null,
      }
    case "websiteIcon":
      return {
        imgUrl: metadatas["icon"],
        width: null,
        height: null,
      }
    default:
      return ""
  }
}

function handleSubmit(
  form: FormData,
  setStatus: (status: StateSubmitStatus) => void,
  setMsgInfo,
  setRowId: (id: string) => void,
  currentUrl: string
): void {
  setStatus("submitting")
  var port = chrome.runtime.connect({ name: "submit_form_to_notion" })
  port.postMessage({ type: "initial_data", params: { form, currentUrl } })
  port.onMessage.addListener((x) => {
    switch (x.type) {
      case "submitting":
        break
      case "error":
        setStatus("error_when_submitting")
        port.disconnect()
        break
      case "success_submit":
        //setRowId(x.id.replaceA("-", ""))
        //print("got", x.id.r);
        //print("got", x.id.replace(/-/g, ''))
        setRowId(x.id.replace(/-/g, ""))
        setStatus("success_submit")
        port.disconnect()
        break
    }
  })
}

