const SelectPropertyField = ({
  collection,
  onChangeProperty,
  property,
  daps,
}: {
  collection: Collection
  property?: ModifiedDapProperty
  onChangeProperty: (property: ModifiedDapProperty) => void
  daps: Dap[]
}) => {
  const [value, setValue] = useState(property ? [property] : [])
  const { refreshCollection } = useContext(EditFormContext)

  const colproperties: (ModifiedDapProperty & {
    valid: boolean
  })[] = useMemo(() => {
    let p = []
    for (let k in collection.schema) {
      let prop: Partial<ModifiedDapProperty & { valid: boolean }> =
        collection.schema[k]
      prop.valid = false
      if (
        [
          "text",
          "select",
          "multi_select",
          "url",
          "title",
          "checkbox",
          "relation",
          "number",
        ].includes(prop.type)
      ) {
        prop.valid = true
      }

      prop.id = k
      p.push(prop)
    }
    p.push({ name: "cover", id: "pageCover", type: "image", valid: true })
    p.push({
      name: "cover image",
      id: "pageFrontImage",
      type: "image",
      valid: true,
    })
    p.push({ name: "icon", id: "pageIcon", type: "image", valid: true })
    return p
  }, [collection])

  const alreadyAddedPropertiesId = useMemo(() => {
    let x = {}
    for (let d of daps) {
      if (d.property) {
        x[d.property.id] = true
      }
    }
    return x
  }, [daps, colproperties])

  const properties = useMemo(() => {
    let ps = colproperties.map((e) => ({ ...e }))
    for (let x of ps) {
      if (
        x.id in alreadyAddedPropertiesId &&
        (value.length == 0 || value[0].id != x.id)
      ) {
        x.valid = false
      }
    }
    return ps
  }, [value, alreadyAddedPropertiesId])

  useEffect(() => {
    if (property) {
      //setInputValue(property.name)
      setValue([property])
    }
  }, [property])

  return (
    <>
      <Autocomplete
        multiple
        onOpen={() => {
          print("refreshing collection...")
          refreshCollection()
        }}
        size={"small"}
        value={value}
        blurOnSelect
        options={properties}
        renderTags={(tagValue, getTagProps) =>
          tagValue.map((option, index) => {
            const { key, ...tagProps } = getTagProps({ index })
            return (
              <PropretyName
                option={option}
                key={key}
                data-tag-index={tagProps["data-tag-index"]}
              />
            )
          })
        }
        getOptionLabel={(option) => option.name}
        getOptionSelected={(option, v) => option.name == v.name}
        renderOption={(option) => <PropretyName option={option} />}
        style={{ width: 200 }}
        getOptionDisabled={(option) => option.valid == false}
        onChange={(
          ev,
          value,
          reason:
            | "create-option"
            | "select-option"
            | "remove-option"
            | "blur"
            | "clear"
        ) => {
          if (value.length == 0) {
            setValue([])
            onChangeProperty(null)
          } else {
            let last = value[value.length - 1]
            setValue([last])
            onChangeProperty(last)
          }
        }}
        renderInput={(params) => {
          return (
            <TextField
              className="propertyField"
              onClick={params.inputProps.onMouseDown}
              {...params}
              inputProps={{
                ...params.inputProps,
                onMouseDown: null,
              }}
              placeholder={value.length == 0 ? "select a property..." : ""}
              variant="outlined"
            />
          )
        }}
      />
    </>
  )
}

const DefaultDapValueSelectAsText = ({
  dap,
  onSetOptions,
}: {
  dap: Dap
  onSetOptions: (options: DapOptions) => void
}) => {
  const [value, setValue] = useState("")

  useEffect(() => {
    print("called", value)
    if (dap.options.defaultValue) {
      setValue(dap.options.defaultValue)
    } else {
      setValue("")
      onSetOptions({ defaultValue: "" })
    }
  }, [])

  return (
    <TextField
      value={value}
      placeholder="enter something..."
      onChange={(ev) => {
        setValue(ev.target.value)
      }}
      onBlur={() => {
        print("got blurred", value)
        onSetOptions({ defaultValue: value })
      }}
    />
  )
}

const DefaultDapValueSelectAsCheckBox = ({
  dap,
  onSetOptions,
}: {
  dap: Dap
  onSetOptions: (options: DapOptions) => void
}) => {
  const [value, setValue] = useState(false)

  useEffect(() => {
    print("called", value)
    if (
      (dap.options.defaultValue as any) &&
      typeof dap.options.defaultValue == "boolean"
    ) {
      setValue(dap.options.defaultValue as any)
    } else {
      setValue(false)
      onSetOptions({ defaultValue: false } as any)
    }
  }, [])

  return (
    <>
      <FormControlLabel
        control={
          <Checkbox
            checked={value}
            onChange={(ev) => {
              setValue(ev.target.checked)
              onSetOptions({ defaultValue: ev.target.checked } as any)
            }}
            name={dap.property.name}
            color="primary"
          />
        }
        label={dap.property.name}
      />
    </>
  )
}

function getDefaultOptionOfSelect(dap: Dap) {
  if (!dap.options.defaultSelectId || !dap.property.options) return undefined
  let x = dap.property.options.find(
    (opt) => opt.id == dap.options.defaultSelectId
  )
  return x
}

const DefaultDapValueSelectAsSelect = ({
  dap,
  onSetOptions,
}: {
  dap: Dap
  onSetOptions: (options: DapOptions) => void
}) => {
  const [defaultOption] = useState(getDefaultOptionOfSelect(dap))
  const [value, setValue] = useState(defaultOption ? [defaultOption] : [])
  print("got value", value)
  return (
    <>
      <Autocomplete
        multiple
        value={value}
        options={dap.property.options || []}
        getOptionLabel={(option) => option.value}
        renderOption={(option) => (
          <Chip
            label={option.value}
            style={{ backgroundColor: getChipColor(option) }}
          />
        )}
        renderTags={(tagValue, getTagProps) =>
          tagValue.map((option, index) => {
            const { key, ...tagProps } = getTagProps({ index })
            return (
              <Chip
                key={key}
                data-tag-index={tagProps["data-tag-index"]}
                label={option.value}
                style={{
                  backgroundColor: getChipColor(option),
                }}
              />
            )
          })
        }
        style={{ width: 300 }}
        onChange={(
          ev,
          options,
          reason:
            | "create-option"
            | "select-option"
            | "remove-option"
            | "blur"
            | "clear"
        ) => {
          console.log("render property", options)
          if (options.length == 0) {
            onSetOptions({ defaultSelectId: "" })
            setValue([])
          } else {
            onSetOptions({ defaultSelectId: options[options.length - 1].id })
            setValue([options[options.length - 1]])
          }
        }}
        renderInput={(params) => (
          <TextField
            placeholder={value.length == 0 ? "choose..." : ""}
            {...params}
            variant="outlined"
          />
        )}
      />
    </>
  )
}

function getDefaultOptionsOfMultiSelect(dap: Dap) {
  if (!dap.options.defaultSelectIds || !dap.property.options) return []
  let ids = {}
  dap.options.defaultSelectIds.forEach((id) => (ids[id] = true))
  let xs = dap.property.options.filter((opt) => ids[opt.id])
  return xs
}
const DefaultDapValueSelectAsMultiSelect = ({
  dap,
  onSetOptions,
}: {
  dap: Dap
  onSetOptions: (options: DapOptions) => void
}) => {
  const [defaultOptions] = useState(getDefaultOptionsOfMultiSelect(dap))
  const [value, setValue] = useState(defaultOptions)
  return (
    <>
      <Autocomplete
        multiple
        value={value}
        disableCloseOnSelect={true}
        options={dap.property.options || []}
        getOptionLabel={(option) => option.value}
        style={{ width: 300 }}
        renderOption={(option) => (
          <Chip
            label={option.value}
            style={{ backgroundColor: getChipColor(option) }}
          />
        )}
        onChange={(
          ev,
          options,
          reason:
            | "create-option"
            | "select-option"
            | "remove-option"
            | "blur"
            | "clear"
        ) => {
          setValue(options)
          onSetOptions({ defaultSelectIds: options.map((opt) => opt.id) })
        }}
        limitTags={3}
        renderTags={(tagValue, getTagProps) =>
          tagValue.map((option, index) => {
            const { key, ...tagProps } = getTagProps({ index })
            return (
              <Chip
                key={key}
                data-tag-index={tagProps["data-tag-index"]}
                label={option.value}
                style={{
                  backgroundColor: getChipColor(option),
                  marginLeft: "2px",
                  marginTop: "2px",
                }}
              />
            )
          })
        }
        renderInput={(params) => (
          <TextField
            placeholder={value.length == 0 ? "choose..." : ""}
            {...params}
            variant="outlined"
          />
        )}
      />
    </>
  )
}

const DefaultDapValueSelect = ({
  dap,
  onSetOptions,
}: {
  dap: Dap
  onSetOptions: (options: DapOptions) => void
}) => {
  switch (dap.property.type) {
    case "url":
    case "title":
    case "text":
      return (
        <DefaultDapValueSelectAsText dap={dap} onSetOptions={onSetOptions} />
      )
    case "checkbox":
      return (
        <DefaultDapValueSelectAsCheckBox
          dap={dap}
          onSetOptions={onSetOptions}
        />
      )
    case "select":
      return (
        <DefaultDapValueSelectAsSelect dap={dap} onSetOptions={onSetOptions} />
      )
    case "multi_select":
      return (
        <DefaultDapValueSelectAsMultiSelect
          dap={dap}
          onSetOptions={onSetOptions}
        />
      )
    default:
      return (
        <Txt fontStyle="italic">
          {" "}
          You can't add a default value for an {dap.property.type}
        </Txt>
      )
  }
}

const CustomDisplayDataValue = ({ dap }: { dap: Dap }) => {
  const option = useMemo(() => getDefaultOptionOfSelect(dap), [dap])
  const options = useMemo(() => getDefaultOptionsOfMultiSelect(dap), [dap])

  if (!dap.property || Object.keys(dap.options).length == 0) {
    return <Txt color="#9e9e9e">Set a value or leave it blank</Txt>
  }
  if (dap.options.selectedData) {
    return (
      <Boxf align="center">
        <MagnetIcon style={{}} />{" "}
        <Txt fontSize="14px" ml={0.5}>
          {datas.find((d) => d.key == dap.options.selectedData).name}
        </Txt>
      </Boxf>
    )
  }
  if (dap.options.customCss) {
    return (
      <Boxf align="center">
        <LocalOfferIcon style={{ color: "grey", fontSize: "15px" }} />{" "}
        <Txt fontSize="14px" ml={0.5}>
          custom CSS Selector
        </Txt>
      </Boxf>
    )
  }
  if (typeof dap.options?.defaultValue == "boolean") {
    return <>{dap.options.defaultValue ? "true" : "false"}</>
  }
  if (dap.options.defaultValue) {
    return <>"{dap.options.defaultValue}"</>
  }
  if (dap.options.defaultSelectId) {
    return (
      <>
        <Chip
          label={option.value}
          style={{ cursor: "pointer", backgroundColor: getChipColor(option) }}
        />
      </>
    )
  }
  if (dap.options.defaultSelectIds) {
    return (
      <div style={{ overflow: "hidden" }}>
        {options.map((option) => (
          <Chip
            key={option.id}
            label={option.value}
            style={{
              cursor: "pointer",
              marginRight: "4px",
              marginTop: "2px",
              backgroundColor: getChipColor(option),
            }}
          />
        ))}
      </div>
    )
  }
  return <Txt color="#9e9e9e">Set a value or leave it blank</Txt>
}

