function dapPropertyOptions(dap, property: ModifiedDapProperty) {
  let options: Dap["options"] = {}
  let fieldOptions: Dap["fieldOptions"] = { locked: false }
  if (property) {
    switch (property.type) {
      case "image" as any:
        options = {
          selectedData:
            (property.id == "pageIcon" && "websiteIcon") || "webpageImage",
        }
        fieldOptions = {
          locked: false,
          image: { type: "coverLike", size: "normal" },
        }
        break
      case "title":
        options = {
          selectedData: "webpageTitle",
        }
        break
      case "url":
        options = {
          selectedData: "webpageUrl",
        }
        break
      case "checkbox":
        options = { defaultValue: false } as any
        break
    }
  }
  return { ...dap, property, options, fieldOptions }
}

