const Opener = ({
  onClose,
  onOpen,
  children,
}: {
  onClose?
  onOpen?
  children: ({
    handleOpen,
    handleClose,
    isOpen,
  }: {
    handleOpen: any
    handleClose: any
    isOpen: boolean
  }) => any
}) => {
  const [isOpen, setIsOpen] = React.useState(false)
  return (
    <>
      {children({
        handleOpen: async () => {
          setIsOpen(true)
          onOpen && (await onOpen())
        },
        handleClose: async (data) => {
          setIsOpen(false)
          onClose && (await onClose(data))
        },
        isOpen,
      })}
    </>
  )
}

