function isValidated(errors: any) {
  return Object.keys(errors).length == 0
}
function validate(data: Partial<FormData>) {
  const errors: any = {}
  if (data.name.length == 0) errors.name = "can't be empty"

  if (data.spaceId.length == 0) errors.spaceId = "can't be empty"
  if (!data.collection) errors.collection = "can't be empty"

  if (data.daps.length == 0) errors.daps = "at least one"
  let i = 1
  for (let dap of data.daps) {
    if (!dap.property) {
      errors.dap = `field ${1} miss a property`
      break
    }
    i++
  }
  return errors
}

