function genRandomHash(schema) {
  let x = makeid(4)
  while (x in schema) {
    x = makeid(4)
  }
  return x
}

async function createNewUrlProperty(
  collection: Collection,
  client: DummyNotionClient
) {
  const schema = collection.schema
  print("got", collection)
  const urlPropId = genRandomHash(schema)
  schema[urlPropId] = { name: "URL", type: "url" }
  await client._submitTransaction([
    {
      id: collection.id,
      table: "collection",
      path: [],
      command: "update",
      args: { schema: schema },
    },
  ])
  return { ...schema[urlPropId], id: urlPropId }
}

async function createDefaultDaps(
  collection: Collection,
  client: DummyNotionClient,
  setLoading: (v: boolean) => void
): Promise<Dap[]> {
  let dapTitle: Dap | null = null
  let dapLink: Dap | null = null
  for (let id in collection.schema) {
    let p = collection.schema[id]
    if (p.type == "title") {
      dapTitle = {
        kind: "data",
        key: makeid(4),
        options: {
          selectedData: "webpageTitle",
        },
        property: { ...p, id },
        fieldOptions: { locked: true },
      }
      if (dapLink) break
    }
    if (p.type == "url") {
      dapLink = {
        kind: "data",
        key: makeid(4),
        options: {
          selectedData: "webpageUrl",
        },
        property: { ...p, id },
        fieldOptions: { locked: true },
      }
      if (dapTitle) break
    }
  }
  if (dapLink == null) {
    setLoading(true)
    try {
      dapLink = {
        kind: "data",
        key: makeid(4),
        options: {
          selectedData: "webpageUrl",
        },
        property: await createNewUrlProperty(collection, client),
        fieldOptions: { locked: true },
      }
    } catch (e) {
      console.log("error when creating new url property", e)
    }
    setLoading(false)
  }
  print(dapLink)
  let daps = []
  let dapFrontImage: Dap = {
    kind: "data",
    key: makeid(4),
    options: {
      selectedData: "webpageImage",
    },
    property: {
      name: "front image",
      id: "pageFrontImage",
      type: "image" as any,
    },
    fieldOptions: {
      locked: true,
      image: { size: "normal", type: "coverLike" },
    },
  }
  let dapFrontIcon: Dap = {
    kind: "data",
    key: makeid(4),
    options: {
      selectedData: "websiteIcon",
    },
    property: { name: "icon", id: "pageIcon", type: "image" as any },
    fieldOptions: {
      locked: true,
      image: { size: "normal", type: "coverLike" },
    },
  }
  daps.push(dapFrontIcon)
  daps.push(dapFrontImage)
  if (dapLink) daps.push(dapLink)
  if (dapTitle) daps.push(dapTitle)
  return daps
}
const EditFormContext = React.createContext<{
  refreshCollection: () => Promise<void>
} | null>(null)

const LoadingSkeletonBox = ({ ...bprops }: {} & BoxProps) => {
  return (
    <Box {...bprops}>
      <Boxfc>
        <Skeleton variant="text" height="40px" width="50px" />
        <Skeleton variant="text" height="40px" width="470px" />
        <Skeleton variant="text" height="40px" width="470px" />
        <Skeleton variant="text" height="40px" width="470px" />
      </Boxfc>
    </Box>
  )
}

export const EditForm = ({
  currentUrl,
  ...p
}: OptionParams & { goBack: () => void; currentUrl?: string }) => {
  const [data, _setData] = useState<Partial<FormData>>(emptyFormData)
  const [loading, setLoading] = useState(false)
  const [formId, setFormId] = useState(p.id || null)
  const timerSaveData = React.useRef(null)
  const setData = (f: (d: Partial<FormData>) => Partial<FormData>) => {
    //setTimout
    if (!timerSaveData.current) {
      print("record timeout")
      timerSaveData.current = setTimeout(() => {
        //saveSync()
        print("Save!!!")
        timerSaveData.current = null
      }, 200)
    } else {
      print("ignore timeout")
    }
    return _setData(f)
  }

  const saveSync = async () => {
    print("saveSync")
    await saveFormData(data, p.action, p.id)
    setFormListHasChanged(true)

    if (p.databaseLocked) {
      setCurrentFormHasChanged(true)
    }
    /*if (!data.collection) {
      if (formId) {
        deleteFormData(formId)
        setFormId(null)
      }
    } else {
      const d = await saveFormData(data, formId ? "edit" : "new", formId)
      setFormId(d.id)
    }
    setFormListHasChanged(true)
    if (p.databaseLocked) {
      setCurrentFormHasChanged(true)
    }*/
  }

  useInterval(
    () => {
      print("save pos")
      saveCurrentPopupState({
        pos: "settings",
        url: currentUrl,
        formId: formId,
      })
    },
    1500,
    () => resetCurrentPopupState()
  )

  //const [loaded, setLoaded] = useState(false)
  const { allDone, setLoaded, loadStates } = useLoadState({
    collection_refresh: true,
    formdata: true,
  })
  const [localStatus, setLocalStatus] = useState<
    "loading" | "ok" | "loading_refresh_collection"
  >("loading")

  useEffect(() => {
    if (p.action == "new") {
      setTimeout(() => {
        setLocalStatus("ok")
      }, 200)
    } else {
      ;(async () => {
        if (p.action == "edit") {
          console.log("fetching data...")
          let x = await getFormData(p.id)
          print("got", "data:", data, p, x, p)
          _setData(x)
          setLoaded("formdata")
          setLocalStatus("ok")
        }
      })()
    }
  }, [])

  const updateData = useCallback(
    (nd) => setData((data) => ({ ...data, ...nd })),
    [data]
  )
  const {
    client,
    setFormListHasChanged,
    setCurrentFormHasChanged,
  } = useContext(DataContext)
  const [advancedSettingOpen, setAdvancedSettingOpen] = useState(true)

  const handleRefreshCollection = () => {
    refreshCollection(client, data.collection, data.daps, {
      getTemplatesInfo: true,
      checkTemplateUpToDate: data.template,
    }).then((result) => {
      setData((data) => ({
        ...applyRefreshCollectionOnData(result, data as any),
      }))
    })
  }

  useEffect(() => {
    if (loadStates.formdata == true) {
      if (data.collection) {
        print("perform update...")
        //perform a data update
        handleRefreshCollection()
      }
    }
  }, [loadStates])

  useEffect(() => {}, [data])
  return (
    <>
      <div className="editForm">
        <EditFormContext.Provider
          value={{
            refreshCollection: async () => {
              handleRefreshCollection()
            },
          }}
        >
          <Backdrop
            style={{ color: "white", zIndex: 2 }}
            open={localStatus == "loading_refresh_collection"}
          >
            <CircularProgress color="inherit" />
          </Backdrop>
          <BoxCont py={2} px={4} width="500px">
            <Box minHeight="60px"></Box>

            <AppBar
              style={{
                padding: "8px",
                minWidth: "500px",
                backgroundColor: "white",
                height: "55px",
              }}
            >
              <Boxf align="center" justify="center">
                <>
                  {localStatus == "loading" ? (
                    <>
                      <Skeleton variant="rect" height="40px" width="200px" />
                    </>
                  ) : (
                    <NameInput
                      defaultName={data.name}
                      onChangeName={(name) => updateData({ name })}
                    />
                  )}
                </>
              </Boxf>
              <Box style={{ position: "fixed", right: 10 }}>
                <>
                  {(!data?.collection && (
                    <Button onClick={() => goBack()} variant="text">
                      cancel
                    </Button>
                  )) || (
                    <Button
                      variant="text"
                      onClick={async () => {
                        console.log(">>>>>>>>>>>>>>>>>>", data, p)
                        await saveSync()
                        goBack()
                      }}
                    >
                      save
                    </Button>
                  )}
                </>
              </Box>
            </AppBar>

            {localStatus == "loading" ? (
              <>
                <Boxfc mb={2}>
                  <LoadingSkeletonBox mb={2} />
                  {p.action == "edit" && <LoadingSkeletonBox />}
                </Boxfc>
              </>
            ) : (
              <>
                <Boxfc>
                  <Boxfc>
                    {!(/*p.databaseLocked || */ false) && (
                      <>
                        <H4 mb={0.5} ml={3}>
                          Database
                        </H4>
                        <Box mb={0.5}>
                          <SpaceSelect
                            spaceId={data.spaceId}
                            disabled={/*p.databaseLocked || */ false}
                            onSelectSpaceId={(spaceId) => {
                              if (data.spaceId != spaceId) {
                                updateData({ spaceId, collection: undefined })
                                if (formId) {
                                  print("delete formId")
                                  deleteFormData(formId)
                                }
                              }
                            }}
                          />
                        </Box>

                        <Box mb={0.5}>
                          <CollectionSelect
                            disabled={/*p.databaseLocked || */ false}
                            collection={data.collection}
                            spaceId={data.spaceId}
                            onSelectCollection={async (collection) => {
                              let name = data.name
                              let daps = data.daps
                              if (!collection) {
                                name = "New Form"
                                daps = []
                                updateData({ collection, name, daps })
                                //setLoaded(true)
                                return
                              } else if (
                                data.name == "New Form" //||
                                //data.name.startsWith("Add to ")
                              ) {
                                name = `${
                                  collection.icon &&
                                  !collection.icon.startsWith("http")
                                    ? `${collection.icon} `
                                    : ""
                                }${collection.name[0][0]}`
                              }

                              if (
                                (data.collection &&
                                  data.collection.id != collection.id) ||
                                data.daps.length == 0
                              ) {
                                print("update daps...")
                                //empty daps now!
                                daps = await createDefaultDaps(
                                  collection,
                                  client,
                                  setLoading
                                )
                              }
                              updateData({
                                collection,
                                name,
                                daps,
                                template: undefined,
                              })
                              //setLoaded(true)
                            }}
                            /*onRefreshCollectionStart={() => {
                      setLocalStatus("loading_refresh_collection")
                    }}
                    onRefreshCollectionEnd={(result) => {
                      updateData({
                        ...applyRefreshCollection(result, data.daps),
                      })
                      setLocalStatus("ok")
                    }}*/
                            daps={data.daps}
                          />
                        </Box>
                      </>
                    )}
                    {loading && <LoadingSkeletonBox mt={2} />}
                    {data.collection && (
                      <Box mt={2}>
                        <Boxfc>
                          <H4 mb={0.5} ml={3}>
                            Options
                          </H4>
                          <Box>
                            <TemplatesSelect
                              templateId={data.template?.id || ""}
                              template={data.template}
                              templates={
                                (data.collection as any).templatesInfo || []
                              }
                              onSelectTemplateId={(templateId) => {
                                updateData({
                                  template: (data.collection as any).templatesInfo?.find(
                                    (template) => template.id == templateId
                                  ),
                                })
                              }}
                            />
                          </Box>
                          <Box mt={0.5}>
                            <SavePageContentSelect
                              onSavePageContentToggle={(savePageContent) => {
                                updateData({
                                  savePageContent,
                                })
                              }}
                              savePageContent={data.savePageContent || false}
                            />
                          </Box>
                        </Boxfc>
                        {/*
                      <Accordion
                        expanded={advancedSettingOpen}
                        onChange={() =>
                          setAdvancedSettingOpen(!advancedSettingOpen)
                        }
                        style={{ boxShadow: "none" }}
                      >
                        <AccordionSummary
                          expandIcon={<ExpandMoreIcon />}
                          style={{
                            display: "inline-flex",
                          }}
                        >
                          <div style={{ fontWeight: 600 }}>More Settings</div>
                        </AccordionSummary>
                        <AccordionDetails>
                          
                        </AccordionDetails>
                      </Accordion>
                        */}
                      </Box>
                    )}
                  </Boxfc>
                </Boxfc>
              </>
            )}

            {data.collection && (
              <FormSubmissionContext.Provider
                value={{
                  spaceId: data.spaceId,
                  createNewPageInCollection: (name, collectionId) => {
                    //todo
                    let newPage = {
                      id: uuid(),
                      name,
                    }
                    upsertCachedCollectionPage(newPage, collectionId)
                    client
                      .createRecord(
                        "block",
                        { id: collectionId, _table: "collection" },
                        {
                          id: newPage.id,
                          type: "page",
                          properties: { title: [[name]] },
                        }
                      )
                      .then((e) => {
                        print("page created!", e)
                      })
                    return newPage
                  },
                  getCachedCollectionPages: getCachedCollectionPages,
                  upsertCachedCollectionPage: upsertCachedCollectionPage,

                  /*getCollectionPages: async (collectionId: string) => {
                    //use cache to retrieve last relations cached
                    return []
                  },*/

                  createNewValueForProperty: (propertyId, value, color) => {
                    print("fetch client")
                    print("calling addValueProperty", propertyId, value)
                    let newValue = {
                      id: uuid(),
                      value,
                      color,
                    }
                    //setImportantRefreshing(true)
                    client
                      .addValueToPropertyCollection(
                        data.collection,
                        propertyId,
                        value,
                        newValue.id,
                        newValue.color
                      )
                      .then((e) => {
                        handleRefreshCollection()
                      })
                    return newValue
                  },
                  refreshCollection: async () => {
                    print("calling refresh collection")
                  },
                }}
              >
                <FormEditContext.Provider
                  value={{ collection: data.collection }}
                >
                  <FieldsManager
                    daps={data.daps}
                    onSetDaps={(daps) => updateData({ daps })}
                  />
                </FormEditContext.Provider>
              </FormSubmissionContext.Provider>
            )}
          </BoxCont>
        </EditFormContext.Provider>
      </div>
    </>
  )
}
export default EditForm
